import VueMarkdown from 'vue-markdown'

export default {
  props: {
    actionData: {
      required: true
    },
    paramType: {
      required: true,
      type: String
    },
    apiDefinitions: {
      required: true
    }
  },
  created () {
    this.defineBodyExample()
  },
  data () {
    return {
      bodyExample: null,
      tokens: null
    }
  },
  watch: {
    'actionData' () {
      this.defineBodyExample()
    }
  },
  methods: {
    submit () {
      this.eventBus.$emit('callResource', {actionData: this.actionData, apiDefinitions: this.apiDefinitions})
    },
    fieldUpdated (index, value) {
      if (!this.actionData.parameters[index].isEnum && value === null) {
        value = this.actionData.parameters[index].value
      }
      this.$emit('fieldUpdated', { index: index, value: value })
    },
    buildLabel (parameter) {
      let label = parameter.label

      if (parameter.example) {
        label += (' ' + this.$t('apiExamplesActionFields.exampleAbbreviation') + ' ' + parameter.example)
      } else if (parameter.apiDefault) {
        label += (' ' + this.$t('apiExamplesActionFields.defaultAbbreviation') + ' ' + parameter.apiDefault)
      }
      return label
    },
    defineBodyExample () {
      if (this.paramType === 'body') {
        this.bodyExample = null
        for (var key in this.actionData.parameters) {
          var field = this.actionData.parameters[key]
          if (field.in === 'body') {
            if (field.schema && field.schema.example) {
              this.bodyExample = field.schema.example
            }
          }
        }
      }
    }
  },
  components: {
    VueMarkdown
  }
}



